<template>
  <div class="row">
    <div class="col-12 mt-3">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width my-0 flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">Add Delivery</h5>
          </div>
          <div class="flex-align-center flex-end"></div>
        </div>
        <div class="col-lg-8 col-md-cu-8 pr-lg-0">
          <div class="box">
            <div class="flex-x-between">
              <div class="row">
                <div class="col-12 mb-3">
                  <label for="Media" class="custom-label">Media</label>
                  <file
                    :key="delivery.name"
                    @completed="setImage"
                    :initialData="
                      delivery.image ? [{ image: delivery.image }] : [{}]
                    "
                  />
                </div>
                <div class="col-12 mb-3">
                  <label for="Name" class="custom-label">Order ID</label>
                  <el-input
                    class="w-100"
                    name="Name"
                    v-model="delivery.order_id"
                  ></el-input>
                </div>

                <div class="col-12 mb-3">
                  <label for="" class="custom-label md-text">Delivery</label>
                  <el-select
                    v-model="delivery.delivery_name"
                    filterable
                    remote
                    reserve-keyword
                    class="w-100"
                    allow-create
                    placeholder="Select or create new"
                    :remote-method="getDeliveries"
                    :loading="loadingDelivey"
                  >
                    <el-option
                      v-for="delivery in deliveries"
                      :key="delivery.id"
                      :label="delivery.name"
                      :value="delivery.name"
                    >
                    </el-option>
                  </el-select>
                </div>
                <div class="col-12">
                  <el-button
                    :disabled="!startEditing"
                    :loading="saving"
                    v-ripple.400
                    @click="Post"
                    class="my-0 w-100"
                    type="primary"
                  >
                    <i class="bx bx-save mr-1"></i>Save
                  </el-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { url, mediaUrl } from "../../../core/constant/data";
import { file } from "../../common";

export default {
  data() {
    return {
      url: url,
      mediaUrl: mediaUrl,
      loadingDelivey: false,
      delivery: {
        delivery_name: "",
        order_id: "",
        image: "media/default.png",
      },
      saving: false,
      startEditing: false,
      deliveries: [],
    };
  },
  components: { file },
  watch: {
    delivery: {
      handler: function () {
        this.startEditing = true;
      },
      deep: true,
    },
  },
  mounted() {
    setTimeout(() => {
      this.startEditing = false;
    }, 100);
    this.$store.commit("setIsReady", true);
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    getDeliveries(query) {
      this.loadingDelivery = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("search-delivery/?query=" + query)
        .then((response) => {
          this.loadingDelivery = false;
          this.deliveries = response.data;
        })
        .catch((err) => {
          this.loadingDelivery = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },

    setImage($event) {
      this.delivery.image = $event[0].image;
    },

    Post() {
      this.saving = true;
      this.store = this.store.id;
      this.$axios
        .post("delivery-history/", { data: this.delivery })
        .then(() => {
          this.startEditing = false;
          this.saving = false;
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! Sale has been successfully added.",
          });
          this.delivery = {
            delivery_name: "",
            order_id: "",
            image: "media/default.png",
          };
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
};
</script>

